import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { DarkMode } from "@styled-icons/material/DarkMode";
import { LightMode } from "@styled-icons/material/LightMode";
import { WalletAlt } from "@styled-icons/boxicons-regular/WalletAlt";
import waitForElement from "../../helpers/waitForElement";

const DarkModeIcon = styled(DarkMode)`
  display: block;
  min-height: 30px;
  height: 30px;
  width: 30px;
  min-width: 30px;
  color: #ffffff;
`;
const LightModeIcon = styled(LightMode)`
  display: block;
  min-height: 30px;
  height: 30px;
  width: 30px;
  min-width: 30px;
  color: #1d1d1d;
`;

const DarkModeSwitch = () => {
  const [darkModeIsEnabled, setdarkModeIsEnabled] = useState(false);
  function checkForNines(firstTime) {
    var now = new Date();
    console.log("996", firstTime, now);

    if (firstTime) {
      if (
        (now.getHours() >= 21 && now.getHours() <= 24) ||
        (now.getHours() >= 0 && now.getHours() <= 9)
      ) {
        waitForElement("#img-dark-mode-toggle").then((element) => {
          element.click();
          setdarkModeIsEnabled(true);
        });
      } else {
        setdarkModeIsEnabled(false);
        document.body.dataset.theme = "default";
      }
    }

    if (now.getHours() === 21) {
      if (
        now.getMinutes() === 0 &&
        !document.body.dataset.theme.includes("dark-mode")
      ) {
        waitForElement("#img-dark-mode-toggle").then((element) => {
          setdarkModeIsEnabled(true);
          element.click();
        });
      }
    } else if (now.getHours() === 9) {
      if (
        now.getMinutes() === 0 &&
        document.body.dataset.theme.includes("dark-mode")
      ) {
        waitForElement("#img-dark-mode-toggle").then((element) => {
          setdarkModeIsEnabled(false);
          element.click();
        });
      }
    }
  }

  useEffect(() => {
    checkForNines(true);
    setInterval(checkForNines, 60000);
    return () => {
      console.log("Cleaning up Dark Mode Switch");
      return false;
    };
  }, [typeof window]);
  return (
    <div className="!img-flex !img-flex-row !img-items-center !img-justify-center">
      {darkModeIsEnabled ? <DarkModeIcon /> : <LightModeIcon />}
      <label className="!img-relative !img-flex !img-justify-between !img-items-center img-group !img-p-2 !img-text-xl">
        <input
          type="checkbox"
          id="img-dark-mode-toggle"
          defaultChecked={darkModeIsEnabled}
          className="!img-cursor-pointer !img-absolute !img-left-1/2 !img--translate-x-1/2 !img-w-full !img-h-full img-peer !img-appearance-none !img-rounded-md"
          onClick={(e) => {
            setdarkModeIsEnabled(!darkModeIsEnabled);
            if (e.target.checked) {
              document.body.dataset.theme = "dark-mode";
              // Instead of doing this, just wait for
              waitForElement("#sb_launchpad > img").then(
                (element) =>
                  (element.src =
                    "https://storage.googleapis.com/highlevel-backend.appspot.com/sidebar-v2/icon_launchpad.svg")
              );
            } else {
              document.body.dataset.theme = "default";
              // Instead of doing this, just wait for
              waitForElement("#sb_launchpad > img").then(
                (element) =>
                  (element.src =
                    "data:image/png;base64,")
              );
            }
          }}
        />
        <span className="!img-box-border !img-cursor-pointer !img-w-[4.1rem] !img-h-7 !img-flex !img-items-center !img-flex-shrink-0 !img-p-1 !img-bg-gray-300 !img-rounded-full !img-duration-300 !img-ease-in-out peer-checked:!img-bg-[#544fff] after:!img-w-5 after:!img-h-5 after:!img-bg-white after:!img-rounded-full after:!img-shadow-md after:!img-duration-300 peer-checked:after:!img-translate-x-[175%] group-hover:after:!img-translate-x-1 img-border img-border-[#ffffff20]"></span>
      </label>
    </div>
  );
};

export default DarkModeSwitch;
