function reportToDatadog(metrics, ...args) {
  log("WEB VITALS", metrics, args);
  fetch("https://datadog-logger.donaldwaynemoorejr.workers.dev", {
    method: "POST",
    body: JSON.stringify([
      {
        ddsource: "plugin",
        ddtags: "env:production,version:1.0",
        hostname: "https://datadog-logger.donaldwaynemoorejr.workers.dev",
        message: metrics,
        service: "webvitals"
      }
    ])
  })
    .then((res) => res.text())
    .then((data) => {
      console.log("DATA", data);
    });
}
reportWebVitals(reportToDatadog);

