// React
import React from "react";
import ReactDOM from "react-dom/client";

// Redux
import { Provider } from "react-redux";
import { persistor, store } from "../redux/store";
import { PersistGate } from "redux-persist/integration/react";

// Helpers
import log from "./log";
import waitForElement from "./waitForElement";

// Pass the element as a function/statement
function embed(Element, rootId, targetSelector, position) {
  log("Embed helper was called (Target: " + targetSelector + ")");

  waitForElement(targetSelector).then((target) => {
    if (!document.querySelector("#" + rootId)) {
      let container = document.createElement("div");
      container.id = rootId;

      switch (position) {
        case "beforebegin":
          target.insertAdjacentElement("beforebegin", container);
          break;
        case "afterbegin":
          target.insertAdjacentElement("afterbegin", container);
          break;
        case "beforeend":
          target.insertAdjacentElement("beforeend", container);
          break;
        case "afterend":
          target.insertAdjacentElement("afterend", container);
          break;
        case "prepend":
          target.prepend(container);
          break;
        case "append":
          target.append(container);
          break;
        default:
          target.append(container);
          break;
      }

      if (document.getElementById(rootId)) {
        const root = ReactDOM.createRoot(document.getElementById(rootId));
        root.render(
          <React.StrictMode>
            <Provider store={store}>
              <PersistGate loading={null} persistor={persistor}>
                <Element />
              </PersistGate>
            </Provider>
          </React.StrictMode>
        );
        console.log("Embedded", rootId);
      } else {
        console.log(
          "Root does not exist or did not get injected (ID: " + rootId + ")"
        );
      }
    } else {
      console.log(rootId + " already rendered");
    }
  });
}

export default embed;
